export const toArabic = (num) => {
    return num.toString()
        .replace(/0/g, 'Ù ')
        .replace(/1/g, 'Ù¡')
        .replace(/2/g, 'Ù¢')
        .replace(/3/g, 'Ù£')
        .replace(/4/g, 'Ù¤')
        .replace(/5/g, 'Ù¥')
        .replace(/6/g, 'Ù¦')
        .replace(/7/g, 'Ù§')
        .replace(/8/g, 'Ù¨')
        .replace(/9/g, 'Ù©');

}

export const fromArabic = (num) => {
    return num.toString()
        .replace(/Ù /g, '0')
        .replace(/Ù¡/g, '1')
        .replace(/Ù¢/g, '2')
        .replace(/Ù£/g, '3')
        .replace(/Ù¤/g, '4')
        .replace(/Ù¥/g, '5')
        .replace(/Ù¦/g, '6')
        .replace(/Ù§/g, '7')
        .replace(/Ù¨/g, '8')
        .replace(/Ù©/g, '9');
}

export const toInt = (num) => {
    const res = parseInt(fromArabic(num));

    if (!res || Number.isNaN(res)) {
        return 0;
    }

    return res;
}